<template>
  <p>Reaction Time: {{ displayScore }} s</p>
  <p class="rank">{{ rank }}</p>
</template>

<script>
export default {
    props: ['score'],
    data() {
        return {
            rank: null,
            displayScore: null
        }
    },
    mounted() {
        this.displayScore = this.score / 1000;
        if (this.score < 250) {
            this.rank = 'Ninja Fingers';
        } else if (this.score < 400) {
            this.rank = 'Rapid Reflexes';
        } else {
            this.rank = 'Snail pace...';
        }
    }
}
</script>

<style>
    .rank {
        font-size: 1.4em;
        color: #0faf87;
        font-weight: bold;
    }
</style>